.profile__bio {
  font-weight: normal;
  font-size: 18px;
  line-height: 1.22;
  margin: 0;
  grid-area: grid_bio;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 600px) {
  .profile__bio {
    font-size: 14px;
    text-align: center;
  }
}